import React, { useState, useEffect, useRef, useCallback } from 'react'
import error from '../../../Assets/Images/error.png'
import styles from './CommonError.module.css'


const CommonError = ({delay, errorMessage, id, resetError, hint}) => {

    const [showHint, toggleHint] = useState(false)
    const hintOn = () => toggleHint(true)
    const hintOff = () => toggleHint(false)

    const [hideError, setHide] = useState(false)

    const errorStyle = hideError ? styles.commonError + ' ' + styles.hideError : styles.commonError;
    const transitionStyle = delay ? {transition: `${delay}s`} : {}

    const transitionTime = delay ? delay : 0.5
    const delayReset = useCallback(
        () => {
            setHide(true)
            setTimeout(() => resetError(id), transitionTime*1000)
        }, [transitionTime, resetError, id]
    )

    let timerId = useRef(null)
    useEffect(() => {
        timerId.current = setTimeout(delayReset, 5000)  //ComponentDidMount
        return () => { clearTimeout(timerId.current) }    //ComponentWillUnmount
    }, [delayReset])
    
    const resetErrorClick = () => {
        clearTimeout(timerId.current)
        delayReset()
    }
   
    return (
        <div className={errorStyle} onMouseOver={hintOn} 
            onMouseLeave={hintOff} onClick={resetErrorClick} style={transitionStyle}>
            <img src={error} alt='error icon' />
            <div className={styles.messages}>
                <span>Something went wrone...</span>
                <span className={styles.errorMessage}>{errorMessage}</span>
            </div>
            {showHint && <div className={styles.hint}>Click to hide...</div>}
        </div>
    )
}

export default CommonError
