const initialState = [];
let id = 0;


const errorReducer = (state = initialState, action) => {

    switch (action.type) {

        case SET_ERROR:
            const currentId = id;
            id++;
            const error = {message: action.payload.message, id: currentId}
            return [...state, error]

        case RESET_ERROR:
            return action.id 
                ? state.filter( (el) => el.id !== action.id ) 
                : state.filter((el, index) => index !== 0)

        default:
            return state
    }
}


export default errorReducer

const SET_ERROR = 'SET_ERROR'
export const setError = (payload) => ({type: SET_ERROR, payload})

const RESET_ERROR = 'RESET_ERROR'
export const resetError = (id) => ({type: RESET_ERROR, id})